import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';

export const DialogCallConfirm = (from, onCall, onReject) => {
    confirmAlert({
      title: ' ',
      message: from,
      buttons: [
        {
          label: '',
          onClick: () => onCall()
        },
        {
          label: '',
          onClick: () => onReject()
        }
      ]
    });
  };